:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }

.perspective {
  perspective: 1000px;
  transform-style: preserve-3d;
  transform-origin: top;
}

@media (min-width: 1000px) {

.loading {
    transform-style: preserve-3d;
    will-change: transform
}
    .loading :global {
      animation: spin-in 1s backwards 1 1s;
    }
  }

.cube {
  display: flex;
}

.face1 {
  backface-visibility: hidden;
  background: var(--color__fire);
  transform: rotateY(-90deg) translateX(-100%);
  transform-origin: left;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

@media (max-width: 1000px) {

.face1 {
    display: none
}
  }

.loading .face1::before {
      content: "";
      height: 15vw;
      width: 15vw;
      margin-top: 33vh;
      border-radius: 50%;
      border: 1vw dotted white;
      border-bottom: 0 none;
    }

.loading .face1::before :global {
        animation: spinner 3s backwards infinite linear;
      }

.loaded .face1 {
    display: none;
  }

.face2 {
  backface-visibility: hidden;
}
