
html {
    box-sizing: border-box;
    
}
*, *:before, *:after {
box-sizing: inherit;
}
main {
    display: block;
}
ul, 
li, 
dl, 
dd,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
}
ul, ol {
    list-style: none;
    padding-left: 0;
  }
:link:not(:hover) {
    text-decoration: none;
}
:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }
@keyframes flip-out {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(90deg);
    opacity: 0.4;
  }
}
@keyframes flip-in {
  0% {
    transform: rotateX(-90deg);
    opacity: 0.4;
  }
  100% {
    transform: rotateX(0deg);
  }
}
@keyframes spin-out {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(90deg);
    opacity: 0.4;
  }
}
@keyframes spin-in {
  0% {
    transform: rotateY(90deg) translateX(50%) rotateY(-90deg) translateX(50%)
      rotateY(90deg);
  }
  100% {
    transform: rotateY(0deg);
    transform-origin: 0 25%;
  }
}
@keyframes spinner {
  to {
    transform: rotate(-1turn);
  }
}
@font-face {
  font-family: "Calluna";
  src: url("../fonts/Calluna-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Theinhardt";
  src: url("../fonts/TheinhardtReg.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Theinhardt";
  src: url("../fonts/TheinhardtLight_alt.woff") format("woff");
  font-weight: lighter;
  font-style: normal;
}
:link {
  color: var(--color__ice);
}
:visited {
  color: var(--color__prince);
}
@media screen {
html {
    background: #888;
    font-size: 17px
}
  }
@media print {
html {
    font-size: 8pt
}
  }
body {
  color: var(--color__body);
  font-family: "Theinhardt", sans-serif;
  line-height: var(--line-height__body);
  margin: 0;
  padding: 0;
}
@media (min-width: 1000px) {
body {
    margin: 1em auto
}
  }
.grid {
  margin-bottom: calc(var(--rhythm__vertical) * 2);
}
.grid--tight-bottom {
    margin-bottom: 0;
  }
@media (min-width: 1000px), print {
.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: var(--column-gap);
    grid-template-areas: "one two-three two-three two-three"
}
  }
.grid__header {
    grid-area: one;
  }
.grid__main {
    grid-area: two-three;
  }
@media (min-width: 600px), print {
.grid__sub {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: var(--column-gap);
      grid-template-areas: "two three three"
  }
    }
.grid__sub-info {
    grid-area: two;
    margin-bottom: var(--rhythm__vertical);
  }
.grid__sub-description {
    grid-area: three;
  }
.divider {
  position: relative;
}
.divider::before {
    content: "";
    position: absolute;
    left: 0;
    top: -0.45rem;
    width: 100%;
    border-top: var(--section-decoration);
  }
@page {
  width: 21cm;
  margin: 0.5cm;
}
