:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }

.downloadPDF {
  color: white;
  text-align: center;
}

@media print {

.downloadPDF {
    display: none
}
  }

@media (min-width: 1000px) {

.downloadPDF {
    position: fixed;
    z-index: 9999;
    transform: rotate(45deg);
    top: 2rem;
    right: -7rem
}
  }

.link {
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  padding: 1rem 7em;
  display: block;
  width: 100%;
  color: inherit;
  text-decoration: none;
  background: var(--color__fire);
}

.link:hover {
    background: var(--color__ice);
  }

@media (min-width: 1000px) {

.text {
    display: inline-block;
    vertical-align: middle;
    width: 6em
}
  }

.icon {
  /* from icono font */
  border: 2px solid;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  font-style: normal;
  color: inherit;
  text-align: left;
  text-indent: -9999px;
  direction: ltr;
  width: 26px;
  height: 32px;
  border-radius: 0 0 0 10px;
  margin: 1px 4px 1px 0.5em;
}

.icon::before,
  .icon::after {
    content: "";
    pointer-events: none;
  }

.icon::before {
    position: absolute;
    width: 0;
    height: 0;
    left: -3px;
    bottom: -3px;
    border-width: 5px;
    border-style: solid;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }

.icon::after {
    width: 13px;
    height: 2px;
    box-shadow: inset 0 0 0 32px, 0 -5px 0 0, 0 5px 0 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
